body {
  font-size: 14px;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 320px) {
  .container {
    min-width: 320px;
  }
}
@media (min-width: 768px) {
  .container {
    width: 700px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1040px;
  }
}
.heading__secondary {
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  color: #d41443;
}
@media screen and (max-width: 768px) {
  .heading__secondary {
    font-size: 18px;
    line-height: 0.857;
  }
}

.heading__main {
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
}
@media screen and (max-width: 768px) {
  .heading__main {
    font-size: 34px;
    line-height: 1.147;
  }
}

.button {
  border-radius: 22px;
}
.button--bright {
  background: #fff;
  color: #d41443;
}
.button--color {
  background: #d41342;
  color: #fff;
}/*# sourceMappingURL=main.css.map */